.topbar{
    width: 100%;
    height: 70px;
    background-color: white;
    color: palevioletred;
    position: fixed;
    top: 0;
    z-index: 2;
}
.wrapper{
    padding: 10px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.left{
    display: flex;
    align-items: center;
}

.logo{
    font-size: 40px;
    font-weight: 700;
    text-decoration: none;
    color: inherit;
    margin-right: 40px;
}
            
.itemContainer{
    display: flex;
    align-items: center;
    margin-left: 30px;
}

.icon{
    font-size: 18px;
    margin-right: 5px;
}

.topbar span{
    font-size: 16px;
    font-weight: 600;
}

.hamburger{
        width: 32px;
        height: 25px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        cursor: pointer;
}
.hamburger span{
        width: 100%;
        height: 3px;
        background-color: palevioletred;
}

